body{
  background-color: #000;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.wrapper{
  background-color: #000;
}
.button{
  background-color: red;
  color: #fff;
  padding: 7px;
  border: none;
}
.button:hover{
  background: #c60202;
 transition: background-color 1s ease-out;
  
}
.show-details {
  width: 50%;
  float: right;
  margin-top: 10px;
  background-color: red;
  color: #fff;
}
.card{
  padding: 0;
  background-color: #000;
  color: #fff;
  box-shadow: 0 0 10px red;
}

 .card-header{
  padding: 1rem;
}
 .card-body{
  background-color: #000; 
}
.card-body p, .card-body h5{
  margin: 0;
 
}
input , button{
  border-radius: 0px !important;
}
button{
  display: block;
  width: 100%;
}
tr{
  line-height: 0.8rem;
}

tr.plot{
  line-height: 1.5rem;
}
th{
  width: 80px;
}
.btn-home{
  text-decoration: none !important;
  color: #fff;
}
.btn-home:hover{
  color: #000;
}
@media only screen and (min-width:1600px){
  .wrapper{
    width: 1140px;
  }
}